 initial;\n                }\n\n                .dropdown-cell-value-shadow {\n                    display: block;\n                    height: 0px;\n                    padding: 0 42px 0 10px;\n                }\n            }\n\n            /*\n             * fixes Firefox td height bug on td > dropdown children\n             * bug should only appear on FF but\n             * @supports = scoped to Firefox only\n             * to minimize side effects\n             */\n            @supports (-moz-appearance:none) {\n                td.dropdown .dash-cell-value-container {\n                    height: auto;\n                }\n            }\n\n            th.dash-filter {\n                position: relative;\n\n                & input {\n                    left: 0;\n                    top: 0;\n                    height: 100%;\n                    width: 100%;\n\n                    &.dash-filter--case {\n                        position: relative;\n                        left: auto;\n                        top: auto;\n                        width: auto;\n                        height: 16px;\n                        line-height: 0px;\n                        padding: 1px;\n                    }\n                    &.dash-filter--case--sensitive {\n                        border-color: hotpink;\n                        border-radius: 3px;\n                        border-style: solid;\n                        border-width: 2px;\n                        color: hotpink;\n                    }\n                }\n            }\n\n            th {\n                white-space: nowrap;\n\n                .column-header--clear,\n                .column-header--delete,\n                .column-header--edit,\n                .column-header--hide,\n                .column-header--sort {\n                    .not-selectable();\n                    cursor: pointer;\n                }\n            }\n\n            tr {\n                min-height: 30px;\n                height: 30px;\n            }\n\n            // cell content styling\n            td, th {\n                background-clip: padding-box;\n                padding: 2px;\n                overflow-x: hidden;\n                white-space: nowrap;\n                box-sizing: border-box;\n\n                text-align: right;\n\n                &.phantom-cell {\n                    visibility: hidden;\n                }\n\n                div.dash-cell-value {\n                    display: inline;\n                    vertical-align: middle;\n                    white-space: inherit;\n                    overflow: inherit;\n                    text-overflow: inherit;\n\n                    &.cell-markdown {\n                        text-align: left;\n                        font-family: sans-serif;\n                        display: inline-block;\n\n                        blockquote {\n                            white-space: pre;\n                        }\n                    }\n                }\n            }\n        }\n\n    \t.dash-spreadsheet-inner textarea {\n            white-space: pre;\n\t    }\n\n\t    .dash-spreadsheet-inner table {\n            border-collapse: collapse;\n\n            font-family: monospace;\n            --accent: hotpink;\n            --border: lightgrey;\n            --text-color: rgb(60, 60, 60);\n            --hover: rgb(253, 253, 253);\n            --background-color-ellipses: rgb(253, 253, 253);\n            --faded-text: rgb(250, 250, 250);\n            --faded-text-header: rgb(180, 180, 180);\n            --selected-background: rgba(255, 65, 54, 0.2);\n            --faded-dropdown: rgb(240, 240, 240);\n            --muted: rgb(200, 200, 200);\n\t    }\n\n\t    /* focus happens after copying to clipboard */\n\t    .dash-spreadsheet-inner table:focus {\n            outline: none;\n\t    }\n\n\t    .dash-spreadsheet-inner thead {\n            display: table-row-group;\n\t    }\n\n\t    .elip {\n            text-align: center;\n         